import React, { Component } from "react";
import socketIOClient from "socket.io-client"

class Manage extends Component {


  constructor(props) {
    super(props)

    this.state = {
      orders: [
        {
          place: 1,
          opacity: 0,
          val: '',
        },
        {
          place: 2,
          opacity: 0,
          val: '',
        },
        {
          place: 3,
          opacity: 0,
          val: '',
        },
        {
          place: 4,
          opacity: 0,
          val: '',
        },
      ],
      num: '',
      endpoint: window.location.host,

    }

  }

  send = (orders) => {
    const socket = socketIOClient(this.state.endpoint);
    socket.emit('change orders', orders)
  }

  handleChange(e) {
    this.setState({ num: e.target.value });
  }

  handleClick(i) {

    console.log('i ', i);
    const currentPlace = this.state.orders.slice().sort((a, b) => a.place - b.place)[i].place;
    const orders = this.state.orders.slice()
    let ii = 0;
    orders.forEach((item, index) => {
      if (item.place === currentPlace) ii = index;
    });

    orders[ii].opacity = 0;
    orders[ii].val = orders[ii].val + ' ';

    let k = 0;
    orders.forEach((item) => {
      if (item.opacity === 1) {
        if (item.place > orders[ii].place) {
          item.place = item.place - 1;
          k = k + 1;
        }
      }
    });
    orders[ii].place = orders[ii].place + k;

    // this.send(orders);
    this.setState({ orders: orders })
    this.send(orders);
    this.nameInput.focus();
  }

  handleEnter() {
    const orders = this.state.orders.slice();
    let num = this.state.num;
    if (num === '') {
      let s = 0;
      let max = 1;
      orders.forEach((item, i) => {
        if (item.opacity === 0) {
          s = s + 1;
        } else if (Number(item.val) > max) {
          max = Number(item.val)
        }

      });



      if (s === 4) {
        num = '1'

      } else {
        num = String(max + 1);


      }

    }

    while (orders.some((item) => item.val === num && item.opacity === 1)) {
      if (isNaN(Number(num))) {
        num = '0';
      }
      let num2 = Number(num);
      num = String(num2 + 1);
    }


    let ind = -1;
    // orders.forEach((item,i)=>{
    //   if (item.opacity===0 && ind < 0) {
    //     ind = i;
    //   }
    // });

    let ordd = []
    orders.forEach((item, i) => {
      if (item.opacity === 0) {
        ordd.push({ ...item, indexx: i });
      }
    });

    console.log(ordd);
    if (ordd.length > 0) {
      ordd.sort((a, b) => a.place - b.place);
      ind = ordd[0].indexx;
    }

    console.log('ind ', ind);
    if (ind >= 0) {
      orders[ind].val = num;
      orders[ind].opacity = 1;

    } else {
      let ttt = 0;
      orders.forEach((item, i) => {
        if (item.place === 1) ttt = i;
        item.place = item.place - 1;
      });
      orders[ttt].val = num;
      orders[ttt].place = 4;

    }
    this.setState({ orders: orders, num: '' });
    this.send(orders);


  }

  render() {
    const template = (num) => ` ${num} `
    const sortOrders = this.state.orders.slice().sort((a, b) => a.place - b.place);
    return (
      <div style={{padding: '2vmin'}}>
        <div style={{marginBottom:'10em'}}>
        <a 
        href='/view'
        target="_blank"
        > </a>
        </div>
        <div style={{height:'6em'}}>
        {sortOrders.map((item, i) => {
          if (item.opacity===1)
          return <div style={{ cursor: 'pointer', marginTop:'0.2em',marginBottom:'0.2em' }} key={i} onClick={(e) => this.handleClick(i)} >{template(item.val)} <span style={{color:'red'}}> -- </span></div>
        })}
        </div>
        <div>
        <input
          style={{width:'90%', padding: '0.4em'}}
          autoFocus={true}
          ref={(input) => { this.nameInput = input; }}
          value={this.state.num}
          onChange={(e) => this.handleChange(e)}
          onKeyPress={(e) => { if (e.key === 'Enter') this.handleEnter() }} />
          </div>
      </div>


    );

  }

  componentDidMount() {
    this.send(this.state.orders)



   
  }

}


export default Manage;


