import React, { Component } from 'react';
import './main.css';
const colpak = require('../123.png')
class Block extends Component {

  render() {
    const x = (this.props.place === 1 || this.props.place === 3) ? '5vmin' : '45vmin';
    const y = (this.props.place === 1 || this.props.place === 2) ? '5vmin' : '45vmin';
    const opacity = this.props.opacity;

    if (opacity === 1) {
      return (

        <div className="block" style={{ top: y, left: x }}>
          <img className="colpak" src={colpak} />
          <div className="zakaz"> N {this.props.val}</div>
          <div className="gotov"></div>

        </div>

      );
    } else {
      return (
        <div className="block" style={{ top: y, left: x, opacity: opacity }}>
          <img className="colpak" src={colpak} />
          <div className="zakaz"> N {this.props.val}</div>
          <div className="gotov"></div>

        </div>

      )
    }

  }

}

export default Block;


